import React, { Component } from 'react'
import { Form, Button } from 'react-bootstrap'
import Select from 'react-select'
import loadable from '@loadable/component'
import '../vendor/libs/react-quill/typography.scss'
import '../vendor/libs/react-quill/editor.scss'
import '../vendor/styles/pages/authentication.scss'
import '../vendor/libs/react-select/react-select.scss'

const optionsWho = [
    { value: 'women', label: 'Women', color: '#00B8D9' },
    { value: 'men', label: 'Men', color: '#0052CC'},
    { value: 'lgbtiq', label: 'LGBTIQ', color: '#5243AA' },
    { value: 'kids', label: 'Kids', color: '#FF5630' },
];

const optionsLevel = [
    { value: 'beginner', label: 'Beginner', color: '#00B8D9' },
    { value: 'intermediate', label: 'Intermediate', color: '#0052CC'},
    { value: 'advanced', label: 'Advanced', color: '#5243AA' },
];

// Catch error in IE10
const ReactQuill = loadable(() => new Promise(resolve =>
import('react-quill').then(Component => resolve(Component)).catch(() => resolve(<div />))
))

class Page2 extends Component {
  constructor(props) {
      super(props)
      props.setTitle('Login v3 - Pages')

      this.state = {
          credentials: {
              email: '',
              password: '',
              rememberMe: false
          },
          multiValue: []
      }
  }

    onValueChange(field, e) {
        this.setState({
            credentials: {
                ...this.state.credentials,
                [field]: field === 'rememberMe' ? e.target.checked : e.target.value
            }
        })
    }

    prevent(e) {
        e.preventDefault()
    }

  render() {
      const modules = {
          toolbar: [
              ['bold', 'italic'],
              [{ 'list': 'ordered' }, { 'list': 'bullet' }],
              ['clean']
          ]
      }
    return (
        <div className="authentication-wrapper authentication-3">
            <div className="authentication-inner">

                {/* Side container */}
                {/* Do not display the container on extra small, small and medium screens */}
                <div className="d-none d-lg-flex col-lg-8 align-items-center ui-bg-cover ui-bg-overlay-container p-5" style={{ backgroundImage: `url('${process.env.PUBLIC_URL}/img/bg/ws/wamsport.jpg')`}}>
                    <div className="ui-bg-overlay bg-dark opacity-50"></div>

                    {/* Text */}
                    <div className="w-100 text-white px-5">
                        <h1 className="display-2 font-weight-bolder mb-4" style={{ textAlign: 'center', paddingTop: '500px' }}>EVERY SPORT STARTS WITH W</h1>
                        <div className="text-large font-weight-light" style={{ textAlign: 'center' }}>
                            Sign up, tell us about your club and find your new team members
                        </div>
                    </div>
                    {/* /.Text */}
                </div>
                {/* / Side container */}

                {/* Form container */}
                <div className="d-flex col-lg-4 align-items-center theme-bg-white p-5">
                    {/* Inner container */}
                    {/* Have to add `.d-flex` to control width via `.col-*` classes */}
                    <div className="d-flex col-sm-7 col-md-5 col-lg-12 px-0 px-xl-4 mx-auto">
                        <div className="w-100">

                            {/* Logo */}
                            <div className="d-flex justify-content-center align-items-center">
                                <div className="ui-w-60">
                                    <div className="w-100 position-relative">
                                        <img src="/img/bg/ws/logo/logocolor.png" width="60"/>
                                    </div>

                                </div>
                            </div>
                            {/* / Logo */}

                            <h4 className="text-center text-lighter font-weight-normal mt-5 mb-0">The last step - let your audience know the important details</h4>

                            {/* Form */}
                            <form className="my-5">
                                <Form.Group>
                                    <Form.Label>Training Schedule</Form.Label>
                                    <ReactQuill modules={modules}
                                                onChange={this.onContentChange} />
                                </Form.Group>

                                <Form.Group>
                                    <Form.Label>Who Is It For</Form.Label>
                                    <Select isMulti className="react-select react-select-primary" classNamePrefix="react-select"
                                            options={optionsWho}
                                            defaultValue={this.state.multiValue} />
                                </Form.Group>

                                <Form.Group>
                                    <Form.Label>Level</Form.Label>
                                    <Select isMulti className="react-select react-select-secondary" classNamePrefix="react-select"
                                            options={optionsLevel}
                                            defaultValue={this.state.multiValue} />
                                </Form.Group>

                                <Form.Group>
                                    <Form.Label>What do your new athletes need to bring with them for the first practice? <span className="text-muted">(e.g. special shoes, balls, warm clothes, etc)</span></Form.Label>
                                    <Form.Control />
                                </Form.Group>

                                <Form.Group>
                                    <div className="d-flex justify-content-between align-items-center m-0">
                                        <Button variant="primary">Save</Button>
                                    </div>
                                </Form.Group>
                            </form>
                            {/* / Form */}

                        </div>
                    </div>
                </div>
                {/* / Form container */}

            </div>
        </div>
    )
  }
}

export default Page2
